<template>
  <div class="banner" style="height: 410px;">
    <el-carousel width="100%">
      <el-carousel-item v-for="item in bannerList" :key="item.id" v-if="item.type == 1">
        <img :src="item.pic_url" :alt="item.title">
        <div class="index slide-top">
          <div class="left" v-if="item.position_type == 1">
            <h1>{{ item.title }}</h1>
            <p>{{ item.sub_title }}</p>
          </div>
          <div class="center" v-if="item.position_type === 0">
            <h1>{{ item.title }}</h1>
            <p>{{ item.sub_title }}</p>
          </div>
          <div class="right" v-if="item.position_type === 2">
            <h1>{{ item.title }}</h1>
            <p>{{ item.sub_title }}</p>
          </div>
        </div>
        
        <!-- <div class="indexLizi" :style="{'backgroundImage': 'url('+ item.pic_url +')'}"></div> -->
      </el-carousel-item>
    </el-carousel>
    <div id="indexLizi" class="indexLizi" v-show="bannerList.length !== 0"></div>
  </div>

</template>

<script>
  import scrollReveal from 'scrollreveal'
  import { getBannerList } from '@/api/index'
  // import THREE from 'three'
  import THREE from '../../../../static/plugin/three.js'
  export default {
    data() {
      return {
        bannerList: []
      }
    },
    methods: {
      initBannerList() {
        getBannerList().then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.bannerList = [...res.data]
        }).catch(() => {
        })
      },
      liziInit() {
        console.log(THREE)
        var SCREEN_WIDTH = window.innerWidth - 100
        var SCREEN_HEIGHT = 360
        var SEPARATION = 90
        var AMOUNTX = 30
        var AMOUNTY = 30
        var container
        var particles, particle
        var count
        var camera
        var scene
        var renderer
        var mouseX = 0
        // var mouseY = 0
        var windowHalfX = window.innerWidth / 2
        // var windowHalfY = window.innerHeight / 2
        init()
        this.interval = setInterval(loop, 1000 / 60)
        function init() {
          container = document.createElement('div')
          container.style.position = 'relative'
          container.style.top = '10px'
          document.getElementById('indexLizi').appendChild(container)
          camera = new THREE.Camera(75, SCREEN_WIDTH / SCREEN_HEIGHT, 1, 10000)
          camera.position.z = 1000
          scene = new THREE.Scene()
          renderer = new THREE.CanvasRenderer()
          console.log(renderer)
          renderer.setSize(SCREEN_WIDTH, SCREEN_HEIGHT)
          particles = []
          var i = 0
          var material = new THREE.ParticleCircleMaterial('0x097bdb', 1)
          for (var ix = 0; ix < AMOUNTX; ix++) {
            for (var iy = 0; iy < AMOUNTY; iy++) {
              particle = particles[ i++ ] = new THREE.Particle(material)
              particle.position.x = ix * SEPARATION - ((AMOUNTX * SEPARATION) / 2)
              particle.position.z = iy * SEPARATION - ((AMOUNTY * SEPARATION) / 2)
              scene.add(particle)
            }
          }
          count = 0
          container.appendChild(renderer.domElement)
          document.addEventListener('mousemove', onDocumentMouseMove, false)
          //  document.addEventListener( 'touchmove', onDocumentTouchMove, false );
        }
        function onDocumentMouseMove(event) {
          mouseX = event.clientX - windowHalfX
          // mouseY = event.clientY - windowHalfY
        }
        // function onDocumentTouchMove(event) {
        //   if (event.touches.length === 1) {
        //     event.preventDefault()
        //     mouseX = event.touches[ 0 ].pageX - windowHalfX
        //     mouseY = event.touches[ 0 ].pageY - windowHalfY
        //   }
        // }
        function loop() {
          camera.position.x += (mouseX - camera.position.x) * 0.05
          //   camera.position.y += ( - mouseY - camera.position.y ) * .05;
          camera.position.y = 364
          var i = 0
          for (var ix = 0; ix < AMOUNTX; ix++) {
            for (var iy = 0; iy < AMOUNTY; iy++) {
              particle = particles[ i++ ]
              particle.position.y = (Math.sin((ix + count) * 0.3) * 50) + (Math.sin((iy + count) * 0.5) * 50)
              particle.scale.x = particle.scale.y = (Math.sin((ix + count) * 0.3) + 1) * 2 + (Math.sin((iy + count) * 0.5) + 1) * 2
            }
          }
          renderer.render(scene, camera)
          count += 0.1
        }
      }
    },
    mounted() {
      this.initBannerList()
      this.liziInit()
      const sr = scrollReveal()

      sr.reveal('.slide-top', { origin: 'top', distance: '200px', duration: 1000, reset: true })
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .banner {
    width: 100%;
    position: relative;
    img{
      width: 100%;
      height: 100%;
    }
    .index{
      width: 1200px;
      height: 410px;
      position: absolute;
      color: white;
      left: 50%;
      z-index: 99999999;
      margin-left: -600px;
      top: 100px;
      h1{
        font-size: 40px;
      }
      p{
        margin-top: 25px;
        font-size: 26px;
      }
      .center{
        text-align: center;
      }
      .right{
        text-align: right;
      }
    }
    .indexLizi{
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      z-index: 9;
    }
    /deep/.el-carousel__container {
        height: 410px;
    }
  }
</style>



